<template>
  <el-row>
    <el-col :span="5" :offset="1">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>用户信息</span>
        </div>
        <el-tree
          :data="userList"
          :check-strictly="true"
          ref="funcTree"
          default-expand-all
          :props="defaultProps"
          node-key="id"
          show-checkbox
        ></el-tree>
      </el-card>
    </el-col>
    <el-col :span="5" :offset="1">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>角色信息</span>
        </div>
        <el-select
          v-model="roleId"
          placeholder="请选择角色"
          @change="selectOnTree"
          :maxlength="20"
          class="inputwinth192"
        >
          <el-option
            v-for="item in roleList"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          >
          </el-option>
        </el-select>
        <el-button
          style="padding-left:6px;padding-right:6px"
          @click="onSubmit"
          icon="fa fa-file-text-o" size="small" type="primary"
        >
          保 存
        </el-button>
      </el-card>
    </el-col>
  </el-row>
</template>
<script>
export default {
  data() {
    return {
      defaultProps: {
        children: "children",
        label: "nickName"
      },
      moduleName: "userRole",
      loadingdata: false,
      roleId: "",
      roleList: [],
      userList: []
    };
  },
  created() {
    this.getInitData();
  },
  mounted() {},
  methods: {
    getInitData() {
      var getpath = "/getUserRole";
      this.loadingdata = true;
      this.$ajax.get(this.moduleName + getpath).then(
        res => {
          this.loadingdata = false;
          this.roleList = res.data.roleList;
          this.userList = res.data.userList;
          this.roleId = this.roleList[0].id;
          this.selectOnTree(this.roleId);
        },
        error => {
          this.loadingdata = false;
        }
      );
    },
    selectOnTree(roldId) {
      var getpath = "/findOne?id=" + roldId;
      this.loadingdata = true;
      this.$refs.funcTree.setCheckedKeys([]);
      this.$ajax.get(this.moduleName + getpath).then(
        res => {
          this.loadingdata = false;
          this.$refs.funcTree.setCheckedKeys(res.data);
        },
        error => {
          this.loadingdata = false;
        }
      );
    },
    onSubmit() {
      var getpath = "/saveData?roleId=" + this.roleId;
      this.loadingdata = true;
      this.$ajax
        .post(this.moduleName + getpath, this.$refs.funcTree.getCheckedKeys())
        .then(
          res => {
            this.loadingdata = false;
          },
          error => {
            this.loadingdata = false;
          }
        );
    }
  }
};
</script>



